<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="/">权限管理</a>
      </el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <!-- 添加角色按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRoledialogVisible = true">添加角色</el-button>
        </el-col>
      </el-row>
      <!-- 角色表格区域 -->
      <el-table :data="rolesList" border stripe>
        <el-table-column type="expand">
          <template #default="{row}">
            <el-row
              v-for="(item1,i1) in row.children"
              :key="item1.id"
              :class="['borderBottom',i1===0?'borderTOP':'','center']"
            >
              <el-col :span="5">
                <el-tag closable>{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row
                  v-for="(item2,i2) in item1.children"
                  :key="item2.id"
                  :class="[i2===0?'':'borderTOP','center']"
                >
                  <el-col :span="8">
                    <el-tag type="success" closable>{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="16">
                    <el-tag
                      type="warning"
                      v-for="(item3,i3) in item2.children"
                      :key="item3.id"
                      :class="[i3===0?'':'borderTOP']"
                      closable
                      @close="removeRights(row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作">
          <template #default="{row}">
            <el-button type="primary" icon="el-icon-edit" size="small">编辑</el-button>
            <el-button type="danger" icon="el-icon-delete" size="small">删除</el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="small"
              @click="showTreeDialog(row)"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 权限树形模态框 -->
    <el-dialog
      title="分配权限"
      :visible.sync="rightsTreeDialogVisible"
      width="50%"
      @close="setCloseDialog"
    >
      <el-tree
        :data="rightsList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defaultCheckedKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="rightsTreeDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allocationRight">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 添加角色模态框 -->
    <el-dialog
      title="添加角色"
      :visible.sync="addRoledialogVisible"
      width="50%"
      @close="adddialogVisibleClose"
    >
      <el-form :model="addRoleForm" :rules="addRoleRules" ref="addRuleForm" label-width="100px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="addRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoledialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRole">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rolesList: [],
      rightsTreeDialogVisible: false,
      rightsList: [],
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      defaultCheckedKeys: [],
      roleId: '',
      addRoledialogVisible: false,
      addRoleForm: {
        roleName: '',
        roleDesc: ''
      },
      addRoleRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          { min: 3, max: 8, message: '角色名称在3到8个字符之间', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          { min: 3, max: 10, message: '角色名称在3到10个字符之间', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('获取角色列表失败')
      this.rolesList = res.data
      // console.log(this.rolesList)
    },
    async removeRights (roles, rightId) {
      const confirmResult = await this.$confirm('此操作将永久删除该权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => {
        return err
      })
      if (confirmResult !== 'confirm') return this.$message.info('取消删除用户信息')
      const { data: res } = await this.$http.delete(`roles/${roles.id}/rights/${rightId}`)
      if (res.meta.status !== 200) return this.$message.error('删除权限失败')
      this.$message.success('删除权限成功')
      roles.children = res.data
    },
    async showTreeDialog (role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      // console.log(res)
      if (res.meta.status !== 200) return this.$message.error('获取权限列表失败')
      this.rightsList = res.data
      this.recursion(role, this.defaultCheckedKeys)
      this.rightsTreeDialogVisible = true
    },
    recursion (role, arr) {
      if (!role.children) {
        return arr.push(role.id)
      }
      role.children.forEach(item => {
        this.recursion(item, arr)
      })
    },
    setCloseDialog () {
      this.defaultCheckedKeys = []
    },
    // 分配权限的确认按钮
    async allocationRight () {
      const arr = [...this.$refs.treeRef.getHalfCheckedKeys(), ...this.$refs.treeRef.getCheckedKeys()]
      // console.log(arr)
      const idStr = arr.join(',')
      // console.log(idStr)
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) {
        return this.$message.error('角色权限分配失败')
      }
      // console.log('-------------')
      this.$message.success('角色权限分配成功')
      this.rightsTreeDialogVisible = false
      this.getRolesList()
    },
    // 单击按钮,添加角色
    async addRole () {
      const { data: res } = await this.$http.post('roles', this.addRoleForm)
      // console.log(res)
      if (res.meta.status !== 201) return this.$message.error('添加角色失败')
      this.$message.success('添加角色成功')
      this.getRolesList()
      this.addRoledialogVisible = false
    },
    // 单击添加模态框中的取消按钮重置表单
    adddialogVisibleClose () {
      this.$refs.addRuleForm.resetFields()
    }
  },
  created () {
    this.getRolesList()
  }
}
</script>

<style>
.el-tag {
  margin: 7px;
}
.borderBottom {
  border-bottom: 1px solid #eee;
}
.borderTOP {
  border-top: 1px solid #eee;
}
.center {
  display: flex;
  align-items: center;
}
</style>
